import styles from './CartItem.module.css';
import Button from '../Ui/Button/Button';

const CartItem = props => {
	const price = props.price.toFixed(2);
	const subtotal = price * props.amount;
	return (
		<div className={styles['cart-item']}>
			<div className={styles['cart-item__information']}>
				<div className={styles['cart-item__name']}>{props.name}</div>
				<div className={styles['cart-item__prize']}>${price}</div>
				<div className={styles['cart-item__amount']}>x{props.amount}</div>
				<div className={styles['cart-item__subtotal']}>${subtotal.toFixed(2)}</div>
			</div>
			<div className={styles['cart-item__control']}>
				{props.disable || <Button onClick={props.onRemove} className={styles['cart-item__btn']}>-</Button>}
				{props.disable || <Button onClick={props.onAdd} className={styles['cart-item__btn']}>+</Button>}
			</div>
		</div>
	);
}

export default CartItem;