// posiblemente añadir ref para controlar el scale al agregar un item
import { useContext, useEffect, useState } from 'react';
import CartContext from '../../store/cart-context';
import styles from './Cartbutton.module.css';
import { CartIcon } from '../Ui/Images/SvgImages';


const Cartbutton = props => {
	const [productAdded, setProductAdded] = useState(false);

	const cartCtx = useContext(CartContext);
	
	const {items} = cartCtx

	const numberOfCartItems = items.reduce((curNumb, item) => {
		return curNumb + item.amount;
	}, 0)

	const bntClasses = `${styles.cart} ${!props.className ? '' : props.className} ${!productAdded ? '' : styles.bump}`;

	useEffect(() => {
		if(cartCtx.items.length === 0) {
			return;
		}
		setProductAdded(true);

		const timer = setTimeout(()=>{
			setProductAdded(false);
		}, 300);

		return () => {
			clearTimeout(timer)
		};
	}, [items])

	return(
		<div onClick={props.onClick} className={bntClasses}>
			<div className={styles['cart-icon']}>
				<CartIcon />
			</div>
			<div className={styles['cart-lable']}>
				 Your Cart
			</div>
			<div className={styles['cart-cuantity']}>
				<span>{numberOfCartItems}</span>
			</div>
		</div>
	);
}

export default Cartbutton;