import { useContext, useState } from 'react';
import styles from './Cart.module.css';
import Card from '../Ui/Card/Card';
import Button from '../Ui/Button/Button';
import { CartIcon } from '../Ui/Images/SvgImages';
import Modal from '../Modal/Modal';
import CartItem from './CartItem';
import CartContext from '../../store/cart-context.js';
import OrderForm from '../Orders/OrderForm';


const Cart = props => {
	const [orderOpen, setOrderOpen] = useState(false);
	const [orderContents, setOrderContents] = useState({});

	const cartCtx = useContext(CartContext);

	const totalAmount = cartCtx.totalAmount.toFixed(2);
	const hasItems = cartCtx.items.length > 0;

	const cartItemRemoveHandler = id => {
		cartCtx.removeItem(id)
	}
	const cartItemAddHandler = item => {
		cartCtx.addItem({...item, amount:1});
	}

	const cartItems = cartCtx.items.map(item => (		
		<CartItem
			key={item.id}
			name={item.name}
			amount={item.amount}
			price={item.price}
			onRemove={cartItemRemoveHandler.bind(null, item.id)}
			onAdd={cartItemAddHandler.bind(null, item)}
			disable={orderOpen}
		/>			
	));

	const onOrder = () => {
		const orderProducts = cartCtx.items.map(item => {
			const container = {}
			container.name= item.name;
			container.amount= item.amount;
			return container;
		})
		setOrderContents({
			products: orderProducts,
			total: totalAmount
		});
		setOrderOpen(true);
	}


	return(
		<Modal>
			<Card className={styles['cart-content']}>
				<div className={styles['cart-content__title']}>
					<CartIcon className={styles['cart-icon']} />{ props.title }
				</div>
				<div className={styles['cart-content__content']}>
					<div className={styles['cart-content__items']}>
					{ cartItems.length === 0 ? <p className={styles['cart-content__none']}>No items in cart</p> : cartItems }
					</div>
					<div className={styles['cart-content__amount']}>
						Total Amount: <div className={styles['cart-content__total']}>$ {totalAmount}</div>
					</div>
					<div className={styles['cart-content__control']}>
						{!orderOpen && <Button onClick={props.onClose} className={styles['cart-content__close']}>Close</Button>}
						{hasItems && !orderOpen && <Button onClick={onOrder} className={styles['cart-content__order']}>Order</Button>}
					</div>

				</div>
			</Card>
			{orderOpen && <OrderForm orderData={orderContents} onCancel={setOrderOpen} onSucces={props.onClose} />}
		</Modal>
	);
}

export default Cart;