import {ReactComponent as ImageLogo} from './logo.svg';
import {ReactComponent as ImageDeli} from './deli-food.svg';
import {ReactComponent as IconCart} from './cart.svg';
import {ReactComponent as OrderPerson } from './pizza-guy.svg';
import './Svg.css';

const Logo = props => {
	return (
		<div className={`svg-containter ${!props.className ? '' : props.className}`}>
			<ImageLogo />
		</div>
	)
}

export const DeliFood = props => {
	return (
		<div className={`svg-containter ${!props.className ? '' : props.className}`}>
			<ImageDeli />
		</div>
	)
}

export const CartIcon = props => {
	return (
		<div className={`svg-containter ${!props.className ? '' : props.className}`}>
			<IconCart />
		</div>
	)
}

export const PizzaGuy = props => {
	return (
		<div className={`svg-containter ${!props.className ? '' : props.className}`}>
			<OrderPerson />
		</div>
	)
}

export default Logo;