import React, { useRef, useState } from 'react';
import styles from './MealItemForm.module.css';
import Button from '../Ui/Button/Button';

const MealItemForm = props => {
	const [amountIsValid, setAmountIsValid] = useState(true);
	const amount = useRef();

	const submitHandler = event => {
		event.preventDefault();

		const enteredAmount = amount.current.value;
		const enteredAmountNumber = +enteredAmount;

		if(
			enteredAmount.trim().length === 0 ||
			enteredAmountNumber < 1 ||
			enteredAmountNumber > 5
		) {
			setAmountIsValid(false)
			return;
		}

		props.onAddToCart(enteredAmountNumber);
	}
	

	return(
		<form onSubmit={submitHandler}>
			<label htmlFor="amount">Amount: </label>
			<input id="amount" type="number" min="1" steps='1' max='5' defaultValue="1" ref={amount} />
			<Button className={styles['meals-meal__submit']} >+add</Button>
			{!amountIsValid && <p className={styles['meals-meal__error']}>Please enter a valid amount (1-5)!!!</p>}
		</form>
	);
}

export default MealItemForm
	