import React, { useContext, useEffect, useState } from 'react';

import useHttp from '../../hooks/use-http';

import styles from './MealsList.module.css';
import Card from '../Ui/Card/Card';
import MealItem from './MealItem';
// import ProductsList from '../../store/product-context.js';

const MealsList = props => {
	const {isLoading, requestError, request: fetchMeals} = useHttp();

	const [allOnlineProducts, setAllOnlineProducts] = useState([]);
	// const allProducts = useContext(ProductsList);

	useEffect(() => {

		const manageMeals = meals => {
			const allMeals = []
			//console.log(meals);
			for (const meal in meals) {
		    	allMeals.push({ 
		    		id: meal, 
		    		name: meals[meal].name,
		    		description: meals[meal].description,
		    		price: meals[meal].price,
		    		icon: meals[meal].icon
		    	});
		    }
		    setAllOnlineProducts(allMeals);
		}

		fetchMeals(
			{ url: 'https://react-pizzalovers-default-rtdb.firebaseio.com/meals.json'},
			manageMeals
		)

	}, [fetchMeals])

	
	
	//console.log(allProducts);

	return(
		<Card className={styles.meals}>
			{isLoading && <p>loading..</p>}
			{allOnlineProducts.map(product => {
				return <MealItem 
					key={product.id}
					id={product.id}
					name={product.name}
					icon={product.icon}
					price={product.price}
					description={product.description}
				/>
			})}
			{requestError && <p>requestError</p>}
		</Card>
	);
}

export default MealsList;