import {useState} from 'react';
import styles from './OrderForm.module.css';
import { PizzaGuy } from '../Ui/Images/SvgImages';
import Button from '../Ui/Button/Button';
import useInput from '../../hooks/use-input';
import useHttp from '../../hooks/use-http';

const OderForm = props => {
	const {isLoading, requestError, request: sendOrder} = useHttp();

	const [orderIsSent, setOrderIsSent] = useState(false)

	const {
		value: enteredFirstName,
		isValid: enteredFirstNameIsValid,
		hasError: firstNameInputHasError,
		valueChangeHandler: firstNameChangeHandler,
		inputBlurHandler: firstNameInputBlurHandler,
		reset: resetFirstNameInput
	} = useInput(value => value.trim() !== '' && value.trim().length > 2)

	const {
		value: enteredLastName,
		isValid: enteredLastNameIsValid,
		hasError: lastNameInputHasError,
		valueChangeHandler: lastNameChangeHandler,
		inputBlurHandler: lastNameInputBlurHandler,
		reset: resetLastNameInput
	} = useInput(value => value.trim() !== '' && value.trim().length > 2)

	const {
		value: enteredEmail,
		isValid: enteredEmailIsValid,
		hasError: emailInputHasError,
		valueChangeHandler: emailChangeHandler,
		inputBlurHandler: emailInputBlurHandler,
		reset: resetEmailInput
	} = useInput(value => value.includes('@') && value.includes('.') && value.trim().length > 5)

	const {
		value: enteredDate,
		isValid: enteredDateIsValid,
		hasError: dateInputHasError,
		valueChangeHandler: dateChangeHandler,
		inputBlurHandler: dateInputBlurHandler,
		reset: resetDateInput
	} = useInput(value => value.trim() !== '')

	let formIsValid = enteredLastNameIsValid && enteredLastNameIsValid && enteredEmailIsValid && enteredDateIsValid;
	let orderId = '';
	const order = props.orderData;

	const cancel = () => {
		props.onCancel(false);
	}

	const onConfirmOrder = event => {
		event.preventDefault();

		const fullName = `${enteredFirstName} ${enteredLastName}`;

		const orderBody = {
			order,
			Full_Name: fullName,
			Email: enteredEmail,
			Delivery_date: enteredDate
		}	
		
		const response = data => {
			setOrderIsSent(true);
			orderId = data;
		}

		sendOrder(
	      {
	        url: 'https://react-pizzalovers-default-rtdb.firebaseio.com/orders.json',
	        method: 'POST',
	        headers: {
	          'Content-Type': 'application/json',
	        },
	        body: orderBody
	      },
	      response
	    )

		//console.log(orderBody);
		resetFirstNameInput();
		resetLastNameInput();
		resetEmailInput();
		resetDateInput();
	}

	const inputFirstNameClass = firstNameInputHasError ? 'invalid' : '';
	const inputLastNameClass = lastNameInputHasError ? 'invalid' : '';
	const inputEmailClass = emailInputHasError ? 'invalid' : '';
	const inputDateClass = dateInputHasError ? 'invalid' : '';
	const today = new Date().toISOString().split('T')[0];

	const OrderSent = () => {
		return (<div className={styles['cart-orderform__overlay']}>
					{isLoading && <p>Sending order...</p>}
					{requestError && <p>requestError</p>}
					{!isLoading && !requestError && 
						<div>
							<div className={styles['cart-orderform__success-order']}>Your Order has been recibed!!</div>
							<Button onClick={props.onSucces} className={styles['cart-orderform__button']}>Return to store</Button>
						</div>
					}
				</div>)
	};

	return(
		<div className={`${styles['cart-orderform']} ${props.className ? props.className : ''}`}>
			<form className={styles['cart-orderform__form']} onSubmit={onConfirmOrder}>
				<div>
					<input 
						type="text" 
						placeholder="First Name"
						id="first-name"
						className={styles[`${inputFirstNameClass}`]}
						onChange={firstNameChangeHandler}
						onBlur={firstNameInputBlurHandler}
						value={enteredFirstName}
					/>
					{firstNameInputHasError && <p className={styles['cart-orderform__error']}>First Name must not be empty</p>}
				</div>
				<div>
					<input 
						type="text" 
						placeholder="Last Name"
						id="last-name"
						className={styles[`${inputLastNameClass}`]}
						onChange={lastNameChangeHandler}
						onBlur={lastNameInputBlurHandler}
						value={enteredLastName}
					/>
					{lastNameInputHasError && <p className={styles['cart-orderform__error']}>Last Name must not be empty</p>}
				</div>
				<div>
					<input 
						type="text" 
						placeholder="Email"
						id="email"
						className={styles[`${inputEmailClass}`]}
						onChange={emailChangeHandler}
						onBlur={emailInputBlurHandler}
						value={enteredEmail}
					/>
					{emailInputHasError && <p className={styles['cart-orderform__error']}>Please enter a valid email</p>}
				</div>
				<div>
					<input 
						type="date" 
						placeholder="Delivery Date"
						min={today}
						id="date"
						className={styles[`${inputDateClass}`]}
						onChange={dateChangeHandler}
						onBlur={dateInputBlurHandler}
						value={enteredDate}
					/>
					{dateInputHasError && <p className={styles['cart-orderform__error']}>Please select a valid delivery date</p>}
				</div>
				
				<Button type='submit' className={styles['cart-orderform__button']} disabled={!formIsValid}>Confirm Order</Button>
				<Button onClick={cancel} className={styles['cart-orderform__cancel']}>Back to cart</Button>
				{orderIsSent && <OrderSent />}
			</form>
			<PizzaGuy className={styles['cart-orderform__bg-image']} />
		</div>
	)
}

export default OderForm;