import React, { useContext, useRef } from 'react';
import styles from './MealItem.module.css';
import MealItemForm from './MealItemForm';
import CartContext from '../../store/cart-context';

const MealItem = props => {
	const cartCtx = useContext(CartContext)
	let icon = false;

	if(props.icon) {
		icon = require(`../Ui/Images/${props.icon}`)
	}

	const addToCartHandler = amount => {
		cartCtx.addItem({
			id: props.id,
			name: props.name,
			amount: amount,
			price: props.price
		})
	}
	
	return(
		<div className={`${styles['meals-meal__item']} ${!props.className ? '' : props.className}`} >
			<div className={styles['meals-meal__icon']}>
				{icon && <img src={icon} alt={props.name} />}
			</div>
			<div className={styles['meals-meal__information']}>
				<div className={styles['meals-meal__name']}>
				{ props.name }
				</div>
				<div className={styles['meals-meal__description']}>
					{ props.description }
				</div>
				<div className={styles['meals-meal__price']}>
					${ props.price.toFixed(2) }
				</div>
			</div>
			<div className={styles['meals-meal__form']}>
				<MealItemForm id={props.id} onAddToCart={addToCartHandler} />
			</div>
		</div>
	);
}

export default MealItem;